body{
    background: linear-gradient(to right, #6a11cb, #2575fc);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 5%;
}
.container {
    background-color: white;
    padding: 20px 30px 50px;
    width: 450px;
    border-radius: 10px;
}
.head {
    display: flex;
    gap: 10px;
    align-items: center;

}
.seconddown{
    display: flex;
}
.seconddown input {
    width: 280px;
    height: 30px;
    background-color: transparent;
    border-radius: 30px;
    padding: 5px 5px 5px 10px;
    outline: none;
}
.seconddown button{
    padding: 10px 30px 10px 30px;
    border-radius: 40px;
    margin-left: -60px;
    background-color: orangered;
    border: none;
    outline: none;
}
.list {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 5px;
    margin-top: 30px;
}
.listi {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.flexy {
    display: flex;
    gap: 20px;
    align-items: center;
}
.flexy p {
    margin-top: 5px;
}
